<template>
  <div id="no2">
    <div>
      <mt-header fixed style="background:#fff;width:100%;height:50px">
        <router-link to="/" slot="left">
          <mt-button icon="back" style="color: #000;">确认订单</mt-button>
        </router-link>
      </mt-header>
    </div>
    <div style="margin-top:50px">
      <!--<p style="line-height:50px;color:#000;padding-left: 20px;">
        结账
      </p>-->
    </div>
    <div v-if="list">
      <div style="margin-bottom:10px;height:130px;margin-left: 20px;" v-for="(i,$index) in list" :key="$index">
        <div style="float:left;width:40%;">
          <img :src="i.url" style="width:110px;height:110px;" />
        </div>
        <div style="float:right;width:60%;">
          <p style="font-size:15px;color:#000">{{i.foodnm}}</p>
          <p style="font-size:12px;color:#ccc;line-height:40px">
            {{i.yuexiao}}
            <span>{{i.haopin}}</span>
          </p>
          <p style="color:red;font-size: 14px;">
            ￥{{i.price}}
           <!-- <span style="display:inline-block;line-height:60px">* {{i.num}}</span>-->
          </p>
        </div>
      </div>
    </div>
    <div style="position:fixed;bottom:0;left:0;overflow:hidden;width:100%;height:50px">
      <div
        style="float:left;color:#fff;width:70%;height:50px;line-height:50px;background:rgba(0,0,0,.5);text-align:center;font-size: 18px;"
      >
        总价钱:￥{{totalPrice}}元
        <router-view></router-view>
      </div>
      <div
        style="float:left;width:30%;height:50px;text-align:center;line-height:50px;background:#31bf88;"
      >
        <a style="color:#fff;font-size: 18px;">去支付</a>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  name: "no2",
  data() {
    return {
      list: ""
    };
  },
  computed: {
    ...mapGetters(["totalPrice"])
  },
  created() {
    this.list = this.$store.state.orderList;
    console.log(this.list);
  }
};
</script>
<style>
</style>


