<template>
  <div id="on1">
    <div>
      <button
        @click="jian"
        style="width:20px;height:20px;border-radius:50%;color:#7abbe9;border: none;border:solid 1px #7abbe9;background: #fff;"
      >-</button>
      <input
        type="text"
        style="width:30px;height: 30px;font-size: 18px;text-align:center;border:none;"
        v-model="number" class="input1"
      />
      <button
        @click="jia"
        style="width:20px;height:20px;border-radius:50%;background:#7abbe9;color:#fff;border:none;"
      >+</button>
    </div>
  </div>
</template>
<script>
export default {
  name: "on1",
  props: ["value"],
  computed: {
    number: {
      get() {
        return this.value;
      },
      set(v) {
        this.$emit("input", v);
      }
    }
  },
  methods: {
    jia() {
      this.number++;
    },
    jian() {
      this.number--;
      if (this.number <= 0) {
        this.number = 0;
        return;
      }
    }
  }
};
</script>
<style>
.input1{
  vertical-align: middle;
}
</style>


